.rating {
    display: flex;
    align-items: flex-end;
    font-size: 16px;
    line-height: 1;
  
    @include screen(tablet) {
      font-size: 20px;
    }
  
    &__body {
      position: relative;
  
      &::before {
        content: '★★★★★';
        display: block;
        letter-spacing: 4px;
        -webkit-text-stroke-width: 1px;
        -webkit-text-stroke-color: $text-color-accent;
        color: transparent;
      }
    }
  
    &__active {
      position: absolute;
      width: 0%;
      height: 100%;
      top: 0;
      left: 0;
      overflow: hidden;
  
      &::before {
        content: '★★★★★';
        letter-spacing: 4px;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        color: $text-color-accent;
      }
    }
  
    &__items {
      display: flex;
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
    }
  
    &__item {
      flex: 0 0 20%;
      height: 100%;
      opacity: 0;
    }
  
    &__value {
      font-size: 50%;
      line-height: 1;
      padding: 0 0 0 10px;
    }
  }