.library {
    &__dropdown {
      margin-bottom: 16px;
    }
  
    @include screen(tablet) {
      margin-bottom: 36px;
    }
  
    @include screen(desktop) {
      margin-bottom: 40px;
    }
  
    &__list {
      margin-bottom: 24px;
  
      @include screen(tablet) {
        margin-bottom: 40px;
      }
    }
  
    &__item {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 20px;
      text-align: center;
  
      @include screen(tablet) {
        gap: 24px;
      }
  
      @include screen(desktop) {
        gap: 28px;
      }
  
      @include screen(desktop) {
        margin-bottom: 40px;
      }
    }
  
    &__text {
      font-weight: 400;
      font-size: 20px;
      line-height: 1.4;
  
      @include screen(tablet) {
        ont-size: 24px;
        line-height: 1.33;
      }
  
      @include screen(desktop) {
        ont-size: 32px;
        line-height: 1.13;
      }
    }
  
    &__btn {
      display: block;
      margin-left: auto;
      margin-right: auto;
    }
  }