.dropdown {
    width: 173px;
    // background-color: white;
    // box-shadow: 0 8px 10px 0 rgba(0, 0, 0, 0.3);
    // font-family: 'Lato', sans-serif;
  
    &__switch:checked + &__options-filter &__select {
      transform: scaleY(1);
    }
  
    &__switch:checked + &__options-filter &__filter:after {
      transform: rotate(-135deg);
    }
  
    &__options-filter {
      width: 100%;
      cursor: pointer;
    }
  
    &__filter {
      display: flex;
      position: relative;
      padding: 12px 16px;
      background-color: transparent;
      border: 1px solid #ffffff;
      border-radius: 8px;
      font-weight: 500;
      font-size: 14px;
      line-height: 1.4;
      transition: all $transition-dur-func;
  
      &::after {
        position: absolute;
  
        top: 45%;
        right: 20px;
        content: '';
        width: 10px;
        height: 10px;
        border-right: 2px solid #ffffff;
        border-bottom: 2px solid #ffffff;
        transform: rotate(45deg) translateX(-45%);
        transition: 0.2s ease-in-out;
      }
    }
  
    &__select {
      position: absolute;
      z-index: 10;
      top: 100%;
      left: 0;
      width: 100%;
      margin-top: 5px;
      overflow: hidden;
      border: 1px solid #ffffff;
      border-radius: 8px;
      transform: scaleY(0);
      transform-origin: top;
      transition: all $transition-dur-func;
    }
  
    &__select-option {
      font-weight: 500;
      font-size: 14px;
      line-height: 1.43;
      color: #f8f8f8;
      padding: 14px 20px;
      border-bottom: 1px solid $color-grey;
      background-color: $bg-dark;
      transition: 0.3s;
  
      &:last-of-type {
        border-bottom: 0;
      }
  
      &:hover {
        color: $text-color-accent;
      }
    }
  }