.btn-scroll {
    display: none;
    position: fixed;
    right: 30px;
    bottom: 20px;
    padding: 10px 11px;
    font-weight: $fw-medium;
    font-size: 30px;
    color: $text-color-accent;
    background: transparent;
    border: 2px solid $color-accent;
    border-radius: 50%;
    z-index: 900;
    transition: all $transition-dur-func;
  
    &:focus,
    &:hover {
      color: $text-color-accent;
      background: $color-white;
    }
  }