.movie {
  
    &__list {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
  
      @include screen(tablet) {
        gap: 15px;
      }
    }
  
    &__item {
      width: 100%;
      height: 406px;
      border-radius: 5px;
      display: flex;
      flex-direction: column;
      padding: 12px;
      cursor: pointer;
      transition: box-shadow $transition-dur-func;
  
      &:hover {
        box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, 0.42);
      }
  
      @include screen(tablet) {
        width: 224px;
        height: 325px;
        gap: 4px;
      }
  
      @include screen(desktop) {
        width: 395px;
        height: 574px;
        gap: 6px;
      }
    }
  
    &__title {
      font-weight: 500;
      font-size: 12px;
      line-height: 1.67;
      text-transform: uppercase;
      margin-top: auto;
  
      @include screen(tablet) {
        line-height: 1.5;
      }
  
      @include screen(tablet) {
        font-size: 20px;
        line-height: 1.15;
      }
    }
  
    &__meta {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  
    &__text {
      font-weight: 500;
      font-size: 12px;
      line-height: 1.67;
      color: $text-color-grey;
  
      @include screen(tablet) {
        line-height: 1.5;
      }
  
      @include screen(desktop) {
        font-size: 20px;
        line-height: 1.15;
      }
    }
  
    &__rating {
      font-size: 12px;
  
      @include screen(tablet) {
        font-size: 10px;
      }
  
      @include screen(desktop) {
        font-size: 16px;
      }
    }
  
    &__item--notfound {
      width: 100%;
      text-align: center;
      font-size: 20px;
      line-height: 1.4;
    }
  }