.logo {
    display: flex;
    justify-content: center;
    align-items: center;
  
    @include screen(tablet) {
      gap: 8px;
    }
  
    &__icon {
      width: 32px;
      height: 32px;
  
      @include screen(tablet) {
        width: 48px;
        height: 48px;
      }
    }
  
    &__text {
      display: none;
  
      @include screen(tablet) {
        color: $text-color-primary;
        display: block;
        font-size: 24px;
        line-height: 1.17;
      }
    }
  }